import all_projects from './database/projects';

const getProjects = (skills = null, technologies = null) => {
    var projects = [];
    if (skills) {
        skills.forEach(param => {
            all_projects.forEach(project => {
                const skills_ = project.skills;
                skills_.find(name => {
                    if (name == param) {
                        projects.push(project);
                    }
                })
            })
        });
    }
    if (technologies) {
        technologies.forEach(param => {
            all_projects.forEach(project => {
                const technologies_ = project.technologies;
                technologies_.find(name => {
                    if (name == param) {
                        projects.push(project);
                    }
                })
            })
        });
    }

    if (!skills && !technologies) {
        return all_projects;
    }
    return projects;

}
export default getProjects; 
